.vux-reddot,
.vux-reddot-border,
.vux-reddot-s {
  position: relative;
}
.vux-reddot:after,
.vux-reddot-border:after,
.vux-reddot-s:after {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #f74c31;
  border-radius: 5px;
  right: -3px;
  top: -3px;
  background-clip: padding-box;
}
.vux-reddot-border:before {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #fff;
  border-radius: 5px;
  right: -4px;
  top: -4px;
  background-clip: padding-box;
  padding: 1px;
}
.vux-reddot-s:after {
  width: 6px;
  height: 6px;
  top: -5px;
  right: -5px;
}
.vux-button-group {
  display: box;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
.vux-button-group > a {
  display: block;
  -webkit-box-flex: 1;
              -ms-flex: 1;
          flex: 1;
  width: 100%;
  height: 30px;
  padding: 0;
  font-size: 14px;
  line-height: 31px;
  text-align: center;
  border: 1px solid #04BE02;
  border-width: 1px 1px 1px 0;
  color: #999;
  white-space: nowrap;
  background: #fdfdfd;
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
.vux-button-group > a.vux-button-group-current,
.vux-button-group > a.hover,
.vux-button-group > a:active {
  border-color: #04BE02;
  color: #FFF;
  background: #04BE02;
}
.vux-button-group > a:first-child {
  border-width: 1px;
  border-top-left-radius: 16px;
  border-bottom-left-radius: 16px;
  background-clip: padding-box;
}
.vux-button-group > a:last-child {
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
  background-clip: padding-box;
}
.vux-button-group > a.vux-button-group-current:disabled,
.vux-button-group > a:disabled {
  border-color: #CDCDCD;
