
.header {
  position: fixed;
  padding: 3px 0;
  box-sizing: border-box;
  background-color: floralwhite;
  width: 100%;
  z-index: 99;
}
.header .header-title,.header h1 {
  margin: 0 88px;
  line-height: 40px;
  text-align: center;
  height: 40px;
  font-size: 18px;
  font-weight: 400;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #999999;
}
.header .header-title > span {
  display: inline-block;
}
.header .header-left,.header .header-right {
  position: absolute;
  top: 14px;
  display: block;
  font-size: 14px;
  line-height: 21px;
  color: #999999;
}
.header .header-left a,.header .header-left button,.header .header-right a,.header .header-right button {
  float: left;
  margin-right: 16px;
  color: black;
}
.header .header-left a:active,.header .header-left button:active,.header .header-right a:active,.header .header-right button:active {
  opacity: .5
}
.header .header-left {
  left: 12px
}
.header .header-left .header-back {
  padding-left: 8px
}
.header .header-left .left-arrow {
  position: absolute;
  width: 30px;
  height: 30px;
  top: -5px;
  left: -5px;
}
.header .header-left .left-arrow::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border: 1px solid black;
    border-width: 1px 0 0 1px;
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
    top: 8px;
    left: 7px;
}
.header .header-right {
  right: 10px
}
.header .header-right a,.header .header-right button {
  margin-left: 8px;
  margin-right: 0
}
.header .header-right .header-more:after {
  content: "\2022\0020\2022\0020\2022\0020";
  font-size: 16px;
}
.header-fade-in-right-enter-active {
  -webkit-animation: fadeinR .5s;
          animation: fadeinR .5s;
}
.header-fade-in-left-enter-active {
  -webkit-animation: fadeinL .5s;
          animation: fadeinL .5s;
}
@-webkit-keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
}
100% {
    opacity:1;
    -webkit-transform:translateX(0);
            transform:translateX(0);
}
}
@keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
}
100% {
    opacity:1;
    -webkit-transform:translateX(0);
            transform:translateX(0);
}
}
@-webkit-keyframes fadeinL{
0% {
    opacity: 0;
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes fadeinL{
0% {
    opacity: 0;
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}




